
.invalidar-cita{
    text-align: center;
    margin: $separacion / 2 0 ;
    text-transform: uppercase;
}

.contenido-resumen{

    margin-bottom: $separacion / 2 0;

    p{
        margin-bottom: .5rem;

        span{
            font-weight: $bold;
            color: $azul;
            font-size: 2.4rem;
        }

    }

    .resumen-servicios{

        h3{
            margin: $separacion / 2 0 ;
        }

        .contenedor-servicio{
            margin-bottom: 2rem;
            padding-bottom: 2rem;
            border-bottom: 1px solid lighten($blanco, 10);

            .precio{
                font-weight: $bold;
                color: $azul;
                font-size: 2.4rem;
            }

        }

        
    }
    
}
.total{
    text-align: right;
}